.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.hooks {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  width: 100%;
  color: antiquewhite;
  background-color: #282c34;
  border-top: #61dafb 1px solid;
  padding: 5px;
}

button {
  background-color: #282c34;
  color: antiquewhite;
  border: #61dafb 1px solid;
  border-radius: 5px;
  padding: 5px;
  margin: 5px;
  cursor: pointer;
}

button:hover {
  background-color: #61dafb;
  color: #282c34;
}

span {
  color: #61dafb;
}

.code-image-use-state {
  width: 500px;
  height: 500px;
}

.code-image-use-effect {
  width: 600px;
  height: 800px;
}

.code-image-use-ref {
  width: 600px;
  height: 800px;
}

.code-image-use-reducer {
  width: 700px;
  height: 1800px;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

@media screen and (max-width: 600px) {
  .hooks {
    flex-direction: column;
  }

  .code-image-use-state,
  .code-image-use-ref,
  .code-image-use-effect,
  .code-image-use-reducer {
    width: auto;
    height: auto;
  }
}